<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true">
      <el-form-item>
        <span> <i class="el-icon-location l-style"></i>全部社区</span>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">+ 添加社区</el-button>
      </el-form-item>
      <el-form-item style="float: right;">
        社区共：<span v-text="total"></span>个
      </el-form-item>
    </el-form>
  </el-col>

  <!--列表-->
  <el-table :data="community_id" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;" height="500">
    <el-table-column type="selection" width="50"></el-table-column>
    <el-table-column label="编号" width="50">
      <template slot-scope="scope">
        <span v-text="Number(scope.$index) + 1 + (page - 1) * 10"></span>
      </template>
    </el-table-column>
    <el-table-column prop="province" label="省份"> </el-table-column>
    <el-table-column prop="city" label="城市"> </el-table-column>
    <el-table-column prop="area_name" label="区域"></el-table-column>
    <el-table-column prop="name" label="社区信息" max-width="240"></el-table-column>
    <el-table-column label="排序操作" width="130" fixed="right">
      <template slot-scope="scope">
        <el-button size="medium" type='text' @click.stop="sorttop(scope.$index, scope.row)">至顶 </el-button>
        <el-button size="medium" type='text' @click.stop="sortUp(scope.$index, scope.row)">↑ </el-button>
        <el-button size="medium" type='text' @click.stop="sortDown(scope.$index, scope.row)">↓</el-button>
      </template>
    </el-table-column>
    <el-table-column label="操作" width="150" fixed="right">
      <template slot-scope="scope">
        <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <!--工具条-->
  <el-col :span="24" class="toolbar">
    <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
    <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
    </el-pagination>
  </el-col>

  <!--新增界面-->
  <el-dialog title="社区信息" v-model="addFormVisible" @close="cancel">
    <el-form :model="addForm" style="max-height: 50vh; overflow-y: auto; padding:0px 30px 0 0;" label-width="100px" :rules="addFormRules" ref="addForm">
      <el-row>
        <el-col :span="6">
          <el-form-item prop="province_id" label="社区名称">
            <el-select v-model="addForm.province_id" placeholder="请输入省份" @change="provinceChange($event)">
              <el-option v-for="item in provinceList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item prop="city_id" class="input-con">
            <el-select v-model="addForm.city_id" placeholder="请输入城市" @change="cityChange($event)">
              <el-option v-for="item in cityList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item prop="area" class="input-con">
            <el-select v-model="addForm.area" placeholder="请输入区域">
              <el-option v-for="item in areaList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item prop="name" class="input-con">
            <el-input v-model="addForm.name" auto-complete="off" :maxlength="30" placeholder="请输入社区名称（15字以内）"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item label="产品图片">
        <el-row>
          <el-col :span="5">
            <el-upload class="avatar-uploader" :file-list="viewpager_0" list-type="picture" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove1" :on-success="handleAvatarSuccess1" :before-upload="beforeAvatarUpload">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
          <el-col :span="5">
            <el-upload class="avatar-uploader" list-type="picture" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove2" :on-success="handleAvatarSuccess2" :file-list="viewpager_1" :before-upload="beforeAvatarUpload">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
          <el-col :span="5">
            <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove3" :on-success="handleAvatarSuccess3" :file-list="viewpager_2" :before-upload="beforeAvatarUpload" list-type="picture">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
          <el-col :span="5">
            <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove4" :on-success="handleAvatarSuccess4" :file-list="viewpager_3" :before-upload="beforeAvatarUpload" list-type="picture">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
        </el-row>
        <div class="pictext">上传5张尺寸828*490的jpg/jpeg</div>
      </el-form-item>
      <el-form-item label="宣传广告">
        <el-col :span="5" class="model-banner">
          <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove5" :on-success="handleAvatarSuccess5" :file-list="ad_image" :before-upload="beforeAvatarUpload" list-type="picture">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <div class="pictext"> 上传尺寸776*276的jpg/jpeg</div>
        </el-col>
      </el-form-item>

      <el-form-item label="头图链接">
        <el-row>
          <el-col :span="5" style="margin-right:15px;">
            <el-input type="text" v-model="addForm.link_0"  placeholder="第一张图的链接"> </el-input>
          </el-col>
          <el-col :span="5" style="margin-right:15px;">
            <el-input type="text" v-model="addForm.link_1"  placeholder="第二张图的链接"> </el-input>
          </el-col>
          <el-col :span="5" style="margin-right:15px;">
            <el-input type="text" v-model="addForm.link_2"  placeholder="第三张头图的链接"> </el-input>
          </el-col>
          <el-col :span="5" style="margin-right:15px;">
            <el-input type="text" v-model="addForm.link_3" placeholder="第四张头图的链接"> </el-input>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item label="相关备注">
        <el-input type="textarea" v-model="addForm.note" :maxlength="200" placeholder="请填写社区备注信息（200字以内）"> </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="cancel">取消</el-button>
      <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
    </div>
  </el-dialog>
</section>
</template>

<script>
import Vue from 'vue'
import {
  getSitesList,
  getAddressApi,
  removeSites,
  addSites,
  isToTopSite,
  isToUpSite
} from '../../api/api';

export default {
  data() {
    return {
      community_id: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], //列表选中列
      addFormVisible: false, //新增界面是否显示
      addLoading: false,
      provinceList: [],
      cityList: [],
      areaList: [],
      addFormRules: {
        name: [{
          required: true,
          message: '请输入社区名称（10字以内）',
          trigger: 'blur'
        }]
      },
      addForm: {
        name: '',
        note: '',
        province_id: '',
        city_id: '',
        area: '',
        viewpager_0: '',
        viewpager_1: '',
        viewpager_2: '',
        viewpager_3: '',
        link_0: '',
        link_1: '',
        link_2: '',
        link_3: '',
        ad_image: ''
      },
      viewpager_0: [],
      viewpager_1: [],
      viewpager_2: [],
      viewpager_3: [],
      ad_image: [],
      imageUrl: ''
    }
  },
  methods: {
    // 上移按钮
    sortUp(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
        isToUpSite({
          above_community_id: this.community_id[index - 1].community_id,
          under_community_id: this.community_id[index].community_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getSites()
          }
        })
      }
    },
    // 至顶按钮
    sorttop(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
        isToTopSite({
          community_id: row.community_id,
          is_top: 1
        }).then((res) => {
        console.log(res)
          if (res.data.code === 0) {
            this.getSites()
          }
        })
      }
    },
    // 下移按钮
    sortDown(index, row) {
      if (index === (this.community_id.length - 1)) {
        this.$message({
          message: '已经是列表中最后一个素材！',
          type: 'warning'
        })
      } else {
        isToUpSite({
          above_community_id: this.community_id[index].community_id,
          under_community_id: this.community_id[index + 1].community_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getSites()
          }
        })
      }
    },

    //获取社区列表
    getSites() {
      let para = {
        page: this.page,
        size: 10
      };
      this.listLoading = true;
      getSitesList(para).then((res) => {
        this.community_id = res.data.data;
        this.total = res.data.total;
        this.listLoading = false;
      });
    },
    //pageNum变化事件
    handleCurrentChange(val) {
      this.page = val;
      this.getSites();
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.addFormVisible = true;
      this.viewpager_0.push({
        url: 'http://94.191.23.153/images/' + row.viewpager_0
      })
      this.viewpager_1.push({
        url: 'http://94.191.23.153/images/' + row.viewpager_1
      })
      this.viewpager_2.push({
        url: 'http://94.191.23.153/images/' + row.viewpager_2
      })
      this.viewpager_3.push({
        url: 'http://94.191.23.153/images/' + row.viewpager_3
      })
      this.ad_image.push({
        url: 'http://94.191.23.153/images/' + row.ad_image
      })
      this.addForm = Object.assign({}, row);
      this.initProvince()
      this.provinceChange()
      this.cityChange()
    },
    //显示新增界面
    handleAdd() {
      this.addFormVisible = true;
      this.initProvince()
    },
    cancel() {
      this.addFormVisible = false;
      this.resetForm();
    },
    initProvince() {
      getAddressApi({
        address_id: 0
      }).then((res) => {
        if (res.data.code === 0) {
          this.provinceList = res.data.data
        }
      })
    },
    provinceChange() {
      if (this.addForm.province_id) {
        getAddressApi({
          address_id: this.addForm.province_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.cityList = res.data.data
            this.addForm.city_id = this.cityList[0].id
          }
        })
      }
    },
    cityChange() {
      if (this.addForm.city_id) {
        getAddressApi({
          address_id: this.addForm.city_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.areaList = res.data.data
            this.addForm.area = this.areaList[0].id
          }
        })
      }
    },
    //新增
    addSubmit: function() {
      if (!this.addForm.province_id) {
        this.$message({
          message: '请选择省份！',
          type: 'warning'
        });
        return false;
      }
      if (!this.addForm.city_id) {
        this.$message({
          message: '请选择城市！',
          type: 'warning'
        });
        return false;
      }
      if (!this.addForm.area) {
        this.$message({
          message: '请选择地区！',
          type: 'warning'
        });
        return false;
      }
      if (!this.addForm.viewpager_0 || !this.addForm.viewpager_1 || !this.addForm.viewpager_2 || !this.addForm.viewpager_3) {
        this.$message({
          message: '产品图片必须上传四张！',
          type: 'warning'
        });
        return false;
      }
      if (!this.addForm.ad_image) {
        this.$message({
          message: '宣传广告不能为空！',
          type: 'warning'
        });
        return false;
      }
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.addLoading = true;
            addSites(this.addForm).then((res) => {
              this.addLoading = false;
              this.$message({
                message: '提交成功',
                type: 'success'
              });
              this.addFormVisible = false;
              this.resetForm()
              this.getSites();
            });
          });
        }
      });
    },
    resetForm() {
      this.addForm = {
        name: '',
        note: '',
        province_id: '',
        city_id: '',
        area: '',
        viewpager_0: '',
        viewpager_1: '',
        viewpager_2: '',
        viewpager_3: '',
        link_0: '',
        link_1: '',
        link_2: '',
        link_3: '',
        ad_image: ''
      }
      this.viewpager_0 = []
      this.viewpager_1 = []
      this.viewpager_2 = []
      this.viewpager_3 = []
      this.ad_image = []
      this.provinceList = []
      this.cityList = []
      this.areaList = []
    },
    //删除
    handleDel: function(index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        let para = {
          community_ids: row.community_id
        };
        removeSites(para).then((res) => {
          this.listLoading = false;
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getSites();
        });
      })
    },
    selsChange: function(sels) {
      this.sels = []
      sels.map((item) => {
        this.sels.push(item.community_id)
      })
    },
    //批量删除
    batchRemove: function() {
      this.$confirm('确认删除选中记录吗？', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        let para = {
          community_ids: this.sels.join(',')
        };
        removeSites(para).then((res) => {
          this.listLoading = false;
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getSites();
        });
      })
    },
    beforeAvatarUpload(files) {
      const isJPG = files.type === 'image/jpg';
      const isJPEG = files.type === 'image/jpeg';
      const isPNG = files.type === 'image/png';
      const isGIF = files.type === 'image/gif';
      const isLt2M = files.size / 1024 / 1024 < 2;

      if (!isJPG && !isJPEG && !isPNG && !isGIF && !isGIF) {
        this.$message.error('上传图片格式只能是 JPG/JPEG/PNG/GIF 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isJPG || isJPEG || isPNG || isGIF && isLt2M;
    },
    handleRemove1(fileList) {
      this.addForm.viewpager_0 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess1(file, fileList) {
      this.addForm.viewpager_0 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove2(fileList) {
      this.addForm.viewpager_1 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess2(file, fileList) {
      this.addForm.viewpager_1 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove3(fileList) {
      this.addForm.viewpager_2 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess3(file, fileList) {
      this.addForm.viewpager_2 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove4(fileList) {
      this.addForm.viewpager_3 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess4(file, fileList) {
      this.addForm.viewpager_3 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove5(fileList) {
      this.addForm.ad_image = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess5(file, fileList) {
      this.addForm.ad_image = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    }
  },
  mounted() {
    this.getSites();
  }
}
</script>
<style  scoped >
.input-con {
  margin-left: -80px;
}

.l-style {
  color: #0082E6;
  margin-right: 5px;
}


.avatar-uploader-icon {
  font-size: 32px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px solid #c0ccda;
  border-radius: 6px;
  box-sizing: border-box;
}
.small-avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 60px;
  text-align: center;
}

.pictext {
  display: block;
  margin-top: -8px;
  font-size: 12px;
  color: #999;
  width: 200px;
  height: 25px;
  margin-bottom: 15px;
  margin-left: 0px;
}

.model-banner .avatar-uploader-icon {
  font-size: 26px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px solid #c0ccda;
  border-radius: 6px;
  box-sizing: border-box;
}

.avatar-uploader {
  display: inline-block;
}
</style>



